.dg_grid_container {
  width: 100%;
  max-width: 1200px;
  margin-right: auto;
  margin-left: auto;
}

.dg_grid_row:after {
  content: '';
  display: table;
  clear: both;
}

.dg_grid_col {
  float: left;
  min-height: 1px;
  padding-left: 12px;
  padding-right: 12px;
  box-sizing: border-box;
}

@media (min-width: 641px) {
  .dg_grid_col {
    padding-left: 16px;
    padding-right: 16px;
  }
}

.dg_grid_col[class*="col_1"] {
  width: 8.33333333%;
}

.dg_grid_col[class*="col_2"] {
  width: 16.66666666%;
}

.dg_grid_col[class*="col_3"] {
  width: 24.99999999%;
}

.dg_grid_col[class*="col_4"] {
  width: 33.33333333%;
}

.dg_grid_col[class*="col_5"] {
  width: 41.66666666%;
}

.dg_grid_col[class*="col_6"] {
  width: 50%;
}

.dg_grid_col[class*="col_7"] {
  width: 58.33333333%;
}

.dg_grid_col[class*="col_8"] {
  width: 66.66666666%;
}

.dg_grid_col[class*="col_9"] {
  width: 74.99999999%;
}

.dg_grid_col[class*="col_10"] {
  width: 83.33333333%;
}

.dg_grid_col[class*="col_11"] {
  width: 91.66666666%;
}

.dg_grid_col[class*="col_12"] {
  width: 100%;
}

html,
body {
  height: 100%;
  background-color: #eeeded;
}

body {
  -webkit-font-smoothing: antialiased;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  color: #333333;
}

@media (min-width: 641px) {
  body {
    font-size: 19.2px;
  }
}

ol,
ul,
li,
a,
span {
  font-size: 16px;
}

@media (min-width: 641px) {
  ol,
  ul,
  li,
  a,
  span {
    font-size: 17.6px;
  }
}

p {
  font-size: 16px;
}

@media (min-width: 641px) {
  p {
    font-size: 17.6px;
  }
}

body,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Hind", sans-serif;
}

a {
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-color: transparent;
  color: #333333;
  cursor: pointer;
  outline: none;
  text-decoration: none;
}

a:active, a:hover {
  color: #4d4d4d;
}

img {
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-color: transparent;
}

.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}

.clearfix:after {
  clear: both;
}

.button {
  text-decoration: none;
  display: inline-block;
  margin: 10px 0;
  padding: 5px 10px;
  border: 1px solid #ffffff;
  text-align: center;
  color: #ffffff;
}

.site {
  height: 100%;
  transition: transform 200ms ease-in;
}

.site.active {
  transform: translate3d(15.625rem, 0, 0);
}

@media (min-width: 641px) {
  .site {
    margin-left: 15.625rem;
  }
}

.container_global_menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 15.625rem;
  height: 100%;
  padding-top: 20px;
  padding-bottom: 20px;
  background: #212121;
  transform: translate3d(-100%, 0, 0);
  transition: transform 200ms ease-in;
}

@media (min-width: 641px) {
  .container_global_menu {
    transform: translate3d(0, 0, 0);
  }
}

.container_global_menu ul {
  list-style: none;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.container_global_menu li {
  margin: 0;
}

.container_global_menu a {
  text-decoration: none;
  display: block;
  padding: 12px 15px;
  border-bottom: 1px solid #333333;
  font-size: 16px;
  color: #ffffff;
}

.container_global_menu a:hover {
  background: #080808;
}

.container_global_menu .search-form {
  position: relative;
  width: 100%;
  padding: 12px 15px;
  box-sizing: border-box;
}

.container_global_menu .search-form input {
  outline: none;
  width: 100%;
  padding: 4px 8px 4px 34px;
  font-size: 16px;
}

.container_global_menu .search-form button[type="submit"] {
  outline: none;
  position: absolute;
  top: 13px;
  width: 30px;
  height: 30px;
  margin: 0;
  padding: 0;
}

.container_main_header {
  background-color: #000000;
  color: #ffffff;
}

.js-trigger-menu {
  width: 30px;
  height: 30px;
  background: red;
}

@media (min-width: 641px) {
  .js-trigger-menu {
    display: none;
  }
}

.logo_site {
  display: block;
  text-align: center;
}

body.home .container_hero {
  background-color: red;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}

body.home .container_main_content {
  padding-top: 20px;
  padding-bottom: 20px;
}

body.home .entry-header,
body.home .entry-content,
body.home .entry-footer {
  padding: 10px;
}

body.home article .container_post {
  background-color: #ffffff;
  border: 1px solid #e2e0e0;
}

body.home article .container_post .post_meta {
  padding-bottom: 10px;
  border-bottom: 1px solid #e2e0e0;
  text-transform: uppercase;
  text-align: center;
}

body.home article .container_post .post_meta span:first-child {
  display: block;
  font-size: 28.8px;
}

body.home article .container_post .post_title {
  margin: 10px 0 5px;
}

body.home article .container_post .post_title .entry-title {
  margin: 0;
}

body.home article .container_post .post_title a {
  font-size: 25.6px;
}

body.home article .container_post .post_author span > span {
  color: #ff8800;
}

body.home article .container_post .post_feature_img img {
  max-width: 100%;
  height: 180px;
}
